
.container-slide-login img {
  width: 100%;
}


.container-slide-login .controls {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}

.container-slide-login .controls .control {
  width: 10px;
  height: 10px;
  background-color: var(--contentColor);
  margin: 5px;
  border-radius: 100%;
  cursor: pointer;
}

.container-slide-login .controls .control.active {
  background-color: var(--secondaryColor);
}
