.container-marker-map-info{
  width: 250px;
  min-height: 80px;
  background-color: var(--whiteColor);
}
.container-marker-map{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.container-marker-map-info .options img{
  width: 30px;
}

.popup-marker-map{
  cursor: pointer;
}
.container-marker-map-info .info{
  margin-bottom: 10px;
}
.container-marker-map-info .info .label{
  font-family: RalewaySemiBold;
}
